import "./Contact.scss";
import contactlogo from "../assets/images/contactlogo.png";
// import owner from "../assets/images/owner.png";

const Contact = () => {
  return (
    <div className="contact">
      <div className="border">
        <div className="contact_title">
          <h1>Contact Us</h1>
          <span></span>
          <p>Lorem ipsum dolor sit, amet consectetur adipisicing elit.</p>
        </div>
        <div className="contact_wrapper">
          <form action="#" className="contact_form">
            <div className="forms">
              <input type="text" className="contact_input" placeholder="Name" />
            </div>
            <div className="forms">
              <input
                type="email"
                className="contact_input"
                placeholder="Email"
              />
            </div>
            <div className="forms">
              <input
                type="text"
                className="contact_input"
                placeholder="Message"
              />
            </div>
            <button type="submit">Submit</button>
          </form>
          <div className="contact-info">
            <div className="contact_background">
              <img src={contactlogo} alt="contactlogo" height={"500px"} />
            </div>
            {/* <div className="owner_block">
              <div className="owner_photo">
                <img src={owner} alt="owner" height={'500px'}/>
              </div>
            </div> */}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Contact;
