import "./Works.scss";
import about1 from "../assets/images/about1.jpg";

import { Navigation, Pagination, Scrollbar, A11y } from "swiper";

import { Swiper, SwiperSlide } from "swiper/react";

import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";

const Works = () => {
  return (
    <div className="works">
      <div className="border">
        <div className="works_title">
          <h1>Our works</h1>
          <span></span>
        </div>
        <div className="slider">
          <Swiper
            // install Swiper modules
            // scrollbar={{ draggable: true }}
            modules={[Navigation, Pagination, Scrollbar, A11y]}
            spaceBetween={50}
            slidesPerView={1}
            navigation
            pagination={{ clickable: true }}
            onSwiper={(swiper) => console.log(swiper)}
            onSlideChange={() => console.log("slide change")}
          >
            <SwiperSlide>
              <img src={about1} alt="about1" height={"600px"} />
              {/* <div className="slide_text">
                <h2>Lorem ipsum dolor sit amet consectetur</h2>
                <p>
                  Lorem ipsum, dolor sit amet consectetur adipisicing elit. Nemo
                  voluptate pariatur reiciendis, iste minus numquam ipsum
                  dolorum iure totam ut, dolorem dolores modi explicabo quae
                  aliquid consectetur eum consequatur omnis.
                </p>
              </div> */}
            </SwiperSlide>
            <SwiperSlide>
              <img src={about1} alt="about1" height={"600px"} />
            </SwiperSlide>
            <SwiperSlide>
              <img src={about1} alt="about1" height={"600px"} />
            </SwiperSlide>
            <SwiperSlide>
              <img src={about1} alt="about1" height={"600px"} />
            </SwiperSlide>
          </Swiper>
        </div>
      </div>
    </div>
  );
};

export default Works;
